imiento centrado en la confiabilidad ")]),_c('p',[_vm._v("Busca garantizar la continuidad de la operación y la disponibilidad de los equipos, sin dar mayor importancia a la eficiencia energética de los equipos. Por su parte, el MCEE se diferencia en que se planifican las tareas de mantenimiento para prevenir y corregir caídas en la eficiencia energética. Por lo tanto, en el MCEE una disminución considerable en la eficiencia energética es tratada como un fallo que debe ser corregido. Una de las metodologías más extendidas en Colombia para implementar el MCEE es la planteada en García, Campos y Vanegas (2018). ")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',{staticClass:"mt-2"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema12/tema12-05.png"),"alt":""}})])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('div',{staticClass:"h4"},[_vm._v("Y consiste en:")]),_c('p',{staticClass:"mb-0"},[_vm._v("1. Identificar los Usos Significativos de Energía (USE), que son los equipos con consumo energético más significativo y que, por tanto, representan las oportunidades de mejora que se deben priorizar.")]),_c('p',{staticClass:"mb-0"},[_vm._v("2. Determinar los subsistemas que intervienen en la operación del equipo.")]),_c('p',[_vm._v("3. Definir las funciones de los equipos USE, su funcionamiento, las variables de interés y sus valores en operación normal.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',{staticClass:"mt-2"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema12/tema12-06.png"),"alt":""}})])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('div',{staticClass:"h4"},[_vm._v("Sigue con…")]),_c('p',{staticClass:"mb-0"},[_vm._v("4. Hacer un análisis de falla energética, es decir, identificar cuándo hay una deficiencia en el funcionamiento del equipo que afecta la eficiencia energética y la 